.drop-box {
  .drop-box__drop {
    display: none;
  }

  .drop-box__btn {
    position: relative;

    cursor: pointer;

    &:after {
      content: '';

      position: absolute;
      top: 50%;
      right: 10px;
      z-index: 1;

      width: 0;
      height: 0;

      border-style: solid;
      border-width: 4px 3px 0 3px;
      border-color: $dark_text transparent transparent transparent;

      transform: translateY(-50%);
    }
  }

  .drop-box__drop {
    position: absolute;
    top: 100%;
    left: 50%;
    z-index: 99;

    display: none;
    width: 220px;
    padding-top: 10px;

    transform: translateX(-50%);

    background-color: #fff;
    border-radius: 5px;
  }

  &.open {
    .drop-box__drop {
      display: block;
    }
  }
}