.form.form_register {
  position: relative;
  z-index: 2;

  width: 100%;
  padding-right: 35px;
  padding-left: 35px;

  background-color: #fff;

  @include media-xs {
    padding-right: 15px;
    padding-left: 15px;
  }

  .form-section__inner {
    max-width: 435px;
    margin-right: auto;
    margin-left: auto;
  }

  .form__field {
    margin-bottom: 9px;
  }

  .form__avatar-link {
    display: block;
    width: 105px;
    margin: 0 auto 30px;
  }

  .form__place-line {
    display: flex;
    flex-wrap: wrap;

    @include media(567px) {
      margin-right: -5px;
      margin-left: -5px;
    }

    .form__field {
      width: calc(100% - 146px);


      @include media(567px) {
        width: 50%;
        padding-right: 5px;
        padding-left: 5px;

        &.form__field_street {
          width: 100%;
        }
      }
    }

    .form__field_other {
      width: 65px;
      margin-left: 8px;

      @include media(567px) {
        width: 50%;
        margin-left: 0;

      }
    }
  }

  .form__checkbox-line {
    margin-top: 30px;
    margin-bottom: 40px;
  }

  .form__checkbox {
    margin-top: 10px;

    @include media(null, 768px) {
      white-space: nowrap;
    }
  }

  .form__navigation {
    @include clearfix;
  }
}


.form-section {
  display: none;

  &.current {
    display: block;
  }
}