.form {
  display: block;
  width: 100%;

  .form__field {
    margin-bottom: 16px;
  }

  .form__label {
    margin-bottom: 19px;

    color: #c6c5c5;
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
  }

  .form__input,
  .form__textarea {
    display: block;
    width: 100%;
    padding: 6px 15px;
    height: 48px;

    background-color: white;
    border: 2px solid #e1e1e1;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;

    transition: border .5s;

    &.parsley-error {
      border-color: red;
    }

    &:focus {
      outline: 0;
    }

    @include placeholder {
      color: $light-gray;
      font-size: 14px;
      font-family: $base;
    }

    &:disabled {
      color: $light-gray;
      background-color: $light-gray;
    }
  }

  .form__textarea {
    height: 50px;
    padding: 10px 15px;
    resize: none;
  }

  .select2-container {
    .select2-selection--single {
      height: 48px;

      background-color: #fff;
      border: 2px solid $border;
      border-radius: 0;

      font-size: 12px;
      font-weight: 700;
      line-height: 16px;

      .select2-selection__arrow {
        top: 20px;
        right: 22px;

        width: 14px;
        height: 8px;

        background: url('../images/icons/arrow-down.png') 0 0 no-repeat;

        b {
          display: none;
        }
      }

      .select2-selection__placeholder {
        color: $light-gray;
        font-size: 14px;
      }
    }

    .select2-selection__rendered {
      padding-left: 15px;
      padding-right: 45px;
      font-size: 12px;
      line-height: 46px;
    }
  }

  .form__fop-line {
    @include clearfix;
    margin-right: -5px;
    margin-left: -5px;

    .form__field {
      float: left;
      width: 33.33%;
      padding-right: 5px;
      padding-left: 5px;

      @include media(567px) {
        width: 100%;
      }
    }
  }
}

.parsley-errors-list {
  padding: 0;
  margin: 3px 0 0;

  list-style: none;

  font-size: 12px;
  color: red;
}