.button {
  display: inline-block;
  min-width: 174px;
  height: 52px;

  padding: 0 30px;

  background-color: #fff;
  border: 1px solid $dark_text;
  border-radius: 26px;
  cursor: pointer;

  color: $dark_text;
  font-size: 14px;
  font-weight: 700;
  line-height: 48px;
  text-align: center;
  text-decoration: none;

  transition: all 0.35s ease;

  @include tracking(50);

  &:hover {
    background-color: $dark_text;

    color: #fff;
  }
}

.button-primary {
  border-color: $primary;
  background-color: $primary;
  color: #fff;
  
  &:hover {
    background-color: #fff;
    color: $primary;
  }
}

.button-gray {
  border-color: $light-gray;

  color: $light-gray;
  background-color: transparent;

  &:hover {
    background-color: $light-gray;

    color: $dark_text;
  }
}

.button-white-arrow {
  position: relative;

  min-width: 203px;
  padding: 7px 101px 0 0;
  height: 32px;

  border: 1px solid transparent;
  cursor: pointer;

  color: #a6a6a6;
  font-size: 13px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: left;

  &:hover {
    opacity: 0.8;
    background-color: transparent;

    color: $primary;
  }

  &:before {
    content: '';

    position: absolute;
    top: 50%;
    right: 0;

    width: 83px;
    height: 14px;

    background: url('../images/icons/right-btn-arrow.png') 0 0 no-repeat;

    transform: translateY(-50%);
  }
}

.button-orange-arrow {
  position: relative;

  min-width: 179px;
  width: auto;
  padding-left: 108px;
  padding-top: 8px;
  padding-right: 10px;
  height: 32px;

  background-color: $gold;
  border-color: $gold;
  border-radius: 0;

  cursor: pointer;

  color: #fff;
  font-size: 13px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    opacity: 0.8;
  }

  &:before {
    content: '';

    position: absolute;
    top: 50%;
    left: 13px;

    width: 83px;
    height: 14px;

    background: url('../images/icons/go-btn-arrow.png') 0 0 no-repeat;

    transform: translateY(-50%);
  }
}

.previous,
.next {
  position: relative;

  margin-top: 19px;

  background: none;
  border: none;
  cursor: pointer;

  color: $dark_text;
  font-size: 13px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    color: $primary;
  }

  &:before {
    content: '';

    position: absolute;
    top: 50%;

    width: 83px;
    height: 14px;

    transform: translateY(-50%);
  }
}

.previous {
  padding-left: 100px;

  &:before {
    left: 0;
    background: url('../images/icons/left-btn-arrow.png') 0 0 no-repeat;
  }
}

.next {
  padding-right: 100px;

  &:before {
    right: 0;

    background: url('../images/icons/right-btn-arrow.png') 0 0 no-repeat;
  }
}