.contacts {
  display: flex;
  flex-wrap: wrap;

  .contacts__col {
    position: relative;

    display: flex;
    align-items: center;
    flex-direction: column;
    flex-shrink: 0;

    width: 50%;
    padding-right: 15px;
    padding-left: 15px;

    overflow: hidden;

    @include media-sm {
      width: 100%;
    }
  }

  .contacts__form {
    margin-top: 69px;
  }

  .contacts__info {
    padding-top: 68px;
    padding-bottom: 62px;

    color: #18191b;
    font-size: 14px;
    line-height: 24px;

    .page-title {
      margin-bottom: 50px;
    }

    a {
      color: $dark_text;
      text-decoration: none;
    }
  }

  .contacts__social {
    margin-top: 70px;

    font-size: 14px;

    span {
      display: inline-block;

      color: #18191b;
      font-weight: 700;
      margin-right: 58px;
      vertical-align: middle;

      @include media(567px) {
        display: block;
        margin-right: 0;
      }
    }

    a {
      display: inline-block;

      color: rgba(#18191b, 0.29);
      font-size: 21px;
      text-decoration: none;
      vertical-align: middle;

      &:hover {
        color: $primary;
      }

      + a {
        margin-left: 35px;
      }
    }
  }
}

#map {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;

  width: 100%;
  height: 100%;

  @include media-sm {
    position: static;

    width: 100%;
    height: 300px;
  }
}