.about-banner {
  display: flex;
  align-items: center;
  justify-content: center;

  padding-top: 15px;
  padding-bottom: 15px;
  width: 100%;
  height: 382px;

  background: url('../images/bg/about-bg.jpg') 0 0 no-repeat;
  background-size: cover;

  text-align: center;

  .about-banner__inner {
    padding-right: 15px;
    padding-left: 15px;
  }

  .about-banner__title {
    display: inline-block;
    padding-left: 34px;
    margin-bottom: 31px;

    color: #fffefe;
    font-size: 43px;
    font-weight: 700;
    line-height: 68px;
    text-transform: uppercase;
    @include tracking(25);

    border-left: 9px solid $primary;

    @include media-xs {
      font-size: 24px;
      line-height: 28px;
    }
  }

  .about-banner__export {
    display: flex;
    justify-content: flex-end;

    max-width: 980px;
    margin-right: auto;
    margin-left: auto;

    .download-box__icon {
      margin-right: 11px;
    }

    .download-box__link {
      color: #fffefe;
      font-size: 12px;
    }
  }

  + .content {
    padding-top: 45px;
  }
}

.data-title {
  margin: 79px 0 45px;

  color: #2a2d2e;
  font-size: 48px;
  font-weight: 700;
  line-height: 54px;
  text-align: center;
  @include tracking(25);

  @include media-xs {
    margin-top: 45px;

    font-size: 34px;
    line-height: 36px;
  }
}

.data-arrow {
  text-align: center;

  span {
    display: inline-block;
    width: 44px;
    height: 26px;

    background: url('../images/icons/data-arrow-down.png') 0 0 no-repeat;
  }
}

.count-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  width: 100%;
  max-width: 922px;
  margin: 63px auto;

  @include media-xs {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .count-list__item {
    margin-bottom: 20px;

    text-align: center;

    @include media-xs {
      width: 50%;
      margin-bottom: 35px;
    }

    @include media(567px) {
      width: 100%;
    }
  }

  .count-list__value {
    margin-bottom: 26px;

    color: $gold;
    font-size: 62px;
    font-weight: 700;
    line-height: 56px;

    @include media-xs {
      font-size: 40px;
      line-height: 42px;
    }
  }

  .count-list__text {
    color: $dark_text;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-transform: uppercase;
  }
}

.about-container {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
}

.history-line {
  display: flex;
  justify-content: space-between;
  align-items: center;

  max-width: 920px;
  margin-top: 83px;
  margin-bottom: 93px;

  .history-line__dot {
    width: 7px;
    height: 7px;

    background-color: #404042;
    border-radius: 50%;
  }

  .history-line__active {
    height: 25px;

    text-align: center;
  }

  .history-line__flex {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 178px;
    height: 100%;

    @include media(567px) {
      width: 120px;
    }
  }

  .history-line__hr {
    width: 42px;
    height: 4px;
    background-color: $gold;

    @include media(567px) {
      width: 22px;
    }
  }

  .history-line__circle {
    width: 25px;
    height: 24px;

    background-color: $gold;
    border-radius: 50%;
  }

  .history-line__date {
    margin-top: 22px;

    color: $gold;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    @include tracking(110);
  }
}

.quote {
  .quote__text {
    color: rgba(#2a2d2e, 0.7);
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
  }

  .quote__name {
    margin-top: 26px;

    color: #2a2d2e;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    text-transform: uppercase;

    @include tracking(100);
  }
}

.general-title {
  position: relative;

  padding-left: 20px;
  margin-top: 35px;
  margin-bottom: 35px;

  color: $gold;
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
  text-transform: uppercase;
  @include tracking(110);

  @include media-xs {
    font-size: 18px;
    @include tracking(50);
  }

  &:before {
    content: '';

    position: absolute;
    top: 9px;
    left: 0;
    z-index: 1;

    height: 18px;
    width: 2px;

    background-color: $gold;
  }

  &.general-title_team {
    margin-left: 158px;
    margin-bottom: 51px;

    @include media-xs {
      margin-left: 0;
    }
  }

  &.general-title_news {
    margin-left: 0;
    margin-bottom: 15px;
  }
}



.general-img-box {
  @include media-xs {
    margin-top: 25px;
    text-align: center;
  }
}

.team-info {
  @include clearfix;

  .team-info__left {
    float: left;
    width: 158px;
    padding-right: 30px;

    @include media-xs {
      width: 100%;

      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      text-align: center;
    }
  }

  .team-info__right {
    float: left;
    width: calc(100% - 158px);
    margin-top: -6px;

    @include media-xs {
      width: 100%;
      margin-top: 35px;

      .title {
        text-align: center;
      }
    }
  }

  .team-info__type {
    margin-bottom: 41px;

    color: $dark_text;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    @include tracking(25);

    &.active {
      position: relative;

      color: $gold;

      &:after {
        content: '';

        position: absolute;
        left: 1px;
        bottom: -19px;

        width: 66px;
        height: 3px;
        background-color: $primary;

        @include media-xs {
          left: 50%;
          margin-left: -33px;
          bottom: -5px;
        }
      }
    }

    @include media-xs {
      margin-bottom: 27px;
    }
  }

  .team-info__ability {
    margin-top: 31px;
    margin-bottom: 34px;

    font-size: 18px;
    line-height: 24px;

    @include media-xs {
      text-align: center;
    }
  }
}


.team {
  position: relative;

  min-height: 502px;
  margin-top: -41px;
  padding-left: 7px;

  @include media-sm {
    margin: 0 auto 40px;
    max-width: 365px;
    min-height: 0;
  }

  .team__list {
    @include media(null, 992px) {
      position: absolute;
      top: -71px;
      left: 341px;
      z-index: 1;

      width: 153px;
      height: 502px;

      background: url('../images/bg/circle-line.png') 0 0 no-repeat;
    }

    @include media-sm {
      position: relative;

      display: flex;
      justify-content: space-between;

      width: 100%;
      padding-right: 15px;
      padding-left: 15px;
      margin-top: 25px;
    }
  }

  .team__main {
    width: 365px;
    height: 365px;

    @include media(567px) {
      max-width: 290px;
      max-height: 290px;
      margin-right: auto;
      margin-left: auto;
    }
  }

  .team__item {
    position: absolute;

    width: 60px;
    height: 60px;

    @include media-sm {
      position: static;
    }

    @include media(567px) {
      width: 40px;
      height: 40px;
    }

    &:nth-child(1) {
      top: 16px;
      left: 27px;
    }

    &:nth-child(2) {
      top: 139px;
      right: -16px;
    }

    &:nth-child(3) {
      top: 291px;
      right: -20px;
    }

    &:nth-child(4) {
      top: 415px;
      left: 41px;
    }
  }

  .team__img {
    display: block;
    width: 100%;
    height: 100%;

    object-fit: cover;
  }
}