@font-face {
  font-family: 'AGFatumC';
  src: url('../fonts/AGFatumC.eot');
  src: url('../fonts/AGFatumC.eot?#iefix') format('embedded-opentype'),
  url('../fonts/AGFatumC.svg#AGFatumC') format('svg'),
  url('../fonts/AGFatumC.ttf') format('truetype'),
  url('../fonts/AGFatumC.woff') format('woff'),
  url('../fonts/AGFatumC.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'AGFatumC';
  src: url('../fonts/AGFatumC-Bold.eot');
  src: url('../fonts/AGFatumC-Bold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/AGFatumC-Bold.svg#AGFatumC-Bold') format('svg'),
  url('../fonts/AGFatumC-Bold.ttf') format('truetype'),
  url('../fonts/AGFatumC-Bold.woff') format('woff'),
  url('../fonts/AGFatumC-Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

$base: 'AGFatumC';