.my-event {
  max-width: 860px;
  margin-right: auto;
  margin-left: auto;

  .my-event__box {
    padding: 20px;
    margin-bottom: 20px;

    border: 2px solid $border;
  }

  .my-event__title {
    color: $dark_text;
    font-size: 20px;
    font-weight: 700;

    @include tracking(25);
  }

  .my-event__base {
    position: relative;

    display: flex;
    justify-content: space-between;

    margin: 26px 0 16px;

    @include media(567px) {
      display: block;
    }

    &:before {
      content: '';

      position: absolute;
      bottom: 8px;
      left: 0;
      z-index: 0;

      width: 100%;
      height: 1px;
      border-bottom: 2px dotted $dark_text;
    }
  }

  .my-event__base-key {
    position: relative;
    z-index: 1;

    padding-right: 5px;

    background-color: #fff;

    color: #2d3a4b;
    font-size: 12px;
    font-weight: 700;
    line-height: 25px;
  }

  .my-event__base-value {
    position: relative;
    z-index: 1;

    padding-left: 15px;
    background-color: #fff;

    color: $dark_text;
    font-size: 18px;
    line-height: 25px;
    font-weight: 700;

    @include media(567px) {
      padding-left: 0;
    }
  }

  .my-event__list {
    margin-top: 10px;
    min-height: .01%;
    overflow-x: auto;
    overflow-y: hidden;

    table {
      width: 100%;
      max-width: 100%;
    }

    td {
      white-space: nowrap;
    }
  }

  .my-event__list-title {
    color: $dark_text;
    font-size: 18px;
    font-weight: 700;
    @include tracking(25);

    @include media(567px) {
      font-size: 14px;
    }
  }

  .my-event__name {
    color: #2d3a4b;
    font-size: 12px;
    font-weight: 700;
    line-height: 18px;
  }

  .my-event__price {
    width: 124px;

    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
    text-align: right;
  }

  .my-event__box-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 18px;
    padding-top: 18px;

    border-top: 1px solid $border;

    @include media(567px) {
      display: block;
      text-align: right;

      .my-event__total-price {
        margin-bottom: 20px;
      }
    }
  }

  .my-event__total-price {
    font-size: 18px;
    font-weight: 700;

    span + span {
      margin-left: 20px;
    }
  }

  .my-event__img-line {
    display: flex;
    flex-wrap: wrap;

    margin-right: -11px;
    margin-left: -11px;
  }

  .my-event__img-item {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    width: 33.33%;
    padding-right: 11px;
    padding-left: 11px;
    margin-bottom: 22px;
  }

  .my-event__img {
    flex-shrink: 0;

    display: block;
    max-width: 100%;
    height: auto;
  }

  table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;

    .counter {
      min-width: 112px;
      text-align: center;
    }

    td {
      padding-top: 4px;
      padding-bottom: 4px;
    }

    tr + tr td {
      border-top: 1px solid $border;
    }
  }
}

.counter {
  .counter__btn {
    flex-shrink: 0;

    display: inline-block;
    width: 25px;
    height: 25px;
    padding: 0;

    border: 1px solid $dark_text;
    border-radius: 50%;

    font-size: 15px;
    line-height: 23px;

    background-color: #fff;
    cursor: pointer;

    transition: all 0.35s ease;

    &:hover {
      background-color: $dark_text;
      color: #fff;
    }
  }

  .counter__input {
    flex-shrink: 0;

    width: 29px;
    height: auto;
    padding: 0 2px;

    background: none;
    border: none;

    color: $dark_text;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
  }
}

.avatar {
  width: 105px;
  height: 105px;

  border-radius: 50%;
  background-color: white;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}