.header {
  padding: 26px 0 23px;

  background-color: #111214;

  @include media(568px) {
    padding: 15px 0;
  }

  .header__logo {
    float: left;

    @include media(1024px) {
      margin-left: 30px;
    }
  }

  .header__search {
    float: left;
    width: 125px;
    margin: 5px 0 0 58px;

    @include media(567px) {
      position: absolute;
      top: -100%;
      left: 0;
      width: 100%;
      z-index: 10;

      margin: 0;
      padding: 30px 25px 30px 50px;

      background-color: #111214;

      &.open {
        top: 0;

        transition: top 0.35s ease;
      }
    }
  }

  .header__btn {
    float: right;
    margin-left: 47px;

    @include media-sm {
      display: none;
    }
  }

  .header__link {
    float: right;
    margin-top: 17px;

    color: $light-gray;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
    text-decoration: none;

    @include tracking(50);

    &:hover {
      color: $primary;
    }

    + .header__link {
      margin-right: 65px;
    }

    @include media-sm {
      display: none;
    }
  }

  .header__adaptive-actions {
    display: none;
    float: right;

    @include clearfix;

    a {
      display: flex;
      align-items: center;
      justify-content: center;

      float: left;
      width: 30px;
      height: 30px;

      color: #fff;
      font-size: 18px;
      text-decoration: none;

      &:hover {
        color: $primary;
      }

      + a {
        margin-left: 5px;
      }
    }

    @include media-sm {
      display: block;
    }

    @include media(568px) {
      margin-top: 13px;
    }
  }
}

.logo {
  display: inline-block;
  width: 84px;
  height: 51px;

  background: url('../images/logo.png') 0 0 no-repeat;
}

.search {
  position: relative;

  display: block;
  width: 100%;

  .search__input {
    width: 100%;
    padding: 8px 20px 5px 0;

    color: #fff;

    background-color: transparent;
    border: none;
    border-bottom: 1px solid $light-gray;

    @include placeholder {
      color: $light-gray;
      font-size: 14px;
    }
  }

  .search__btn {
    position: absolute;
    top: 10px;
    right: 0;

    padding: 0;

    background: none;
    border: none;
    cursor: pointer;

    color: $light-gray;
    font-size: 13px;

    transition: color 0.35s ease;

    &:hover {
      color: $primary;
    }
  }
}

.search-toggle {
  float: left;
  display: none;
  padding: 0;
  width: 30px;
  height: 30px;
  margin-right: 5px;

  background-color: #111214;
  border: none;
  cursor: pointer;

  color: #fff;
  font-size: 18px;
  line-height: 30px;

  transition: color 0.35s ease;

  @include media(567px) {
    display: block;
  }

  &:hover {
    color: $primary;
  }

  &.open {
    display: block;
    position: absolute;
    top: 34px;
    left: 15px;
    z-index: 22;

    .fa-times-circle {
      display: inline-block;
    }

    .fa-search {
      display: none;
    }
  }

  .fa-times-circle {
    display: none;
  }
}

.menu-toggle {
  position: absolute;
  top: 33px;
  left: 42px;
  z-index: 10001;

  padding: 0;

  background-color: transparent;
  border: none;
  cursor: pointer;

  color: #959595;
  font-size: 24px;

  transition: color 0.35s ease;

  @include media(1600px) {
    left: 15px;
  }

  &:hover {
    color: $primary;
  }

  &.hidden {
    display: none;
  }
}

.menu {
  position: fixed;
  top: 0;
  left: -332px;
  z-index: 10000;

  display: flex;
  align-items: center;
  justify-content: center;

  @include media-xs {
    display: block;
    width: 320px;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  width: 332px;
  height: 100vh;

  overflow: auto;

  border-right: 2px solid $border;
  background-color: $dark_text;

  transition: left 0.5s ease-in-out;

  &.open {
    left: 0;
  }

  .menu__item {

    @include media-xs {
      width: 210px;
      margin-right: auto;
      margin-left: auto;
    }

    + .menu__item {
      margin-top: 45px;
    }
  }

  .menu__link {
    position: relative;

    display: inline-block;

    color: #fff;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    text-decoration: none;

    @include tracking(50);

    &.active {
      color: $primary;

      &:after {
        content: '';

        position: absolute;
        bottom: -22px;
        left: 0;

        width: 66px;
        height: 3px;

        background-color: $primary;
      }
    }
  }
}

.social {
  position: fixed;
  z-index: 1;
  bottom: 240px;

  width: 121px;

  @include media(1600px) {
    width: 51px;
  }

  @include media(1200px) {
    bottom: 149px;
  }

  @include media-xs {
    display: none;
  }

  .social__item {
    text-align: center;
    + .social__item {
      margin-top: 32px;
    }
  }

  a {
    display: inline-block;

    color: #fff;
    font-size: 21px;
    text-decoration: none;
    vertical-align: middle;

    &:hover {
      color: $primary;
    }
  }
}

.slider-btn {
  position: fixed;
  bottom: 99px;
  left: 0;
  z-index: 4;

  display: flex;

  background-color: #be965d;

  .slider-btn__left,
  .slider-btn__right {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    justify-content: center;

    width: 121px;
    height: 166px;

    cursor: pointer;

    @include media(1600px) {
      width: 51px;
      height: 96px;
    }

    &:hover {

      opacity: .8;
    }

    font-size: 18px;
  }

  .slider-btn__left {
    background-color: white;
    color: rgba(#000, 0.15);
  }

  .slider-btn__right {
    background-color: $primary;
    color: #000;
  }

  &:after {
    content: '';

    position: absolute;
    left: 121px;
    bottom: -99px;
    z-index: 2;

    width: 1px;
    height: 100vh;

    opacity: 0.37;
    background-color: #959595;

    @include media(1600px) {
      left: 51px;
    }
  }

  @include media(1200px) {
    display: none;
  }
}