.calendar-slider-item {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;

  border-bottom: 1px solid #d1d5db;

  .calendar-slider-item__img-box {
    flex-shrink: 0;

    display: block;
    width: 70px;
    height: 70px;
    margin-top: 5px;
    margin-right: 34px;
  }

  .calendar-slider-item__img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .calendar-slider-item__info {
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    justify-content: space-between;

    width: calc(100% - 104px);
  }

  .calendar-slider-item__title {
    height: 42px;
    overflow: hidden;

    color: $dark_text;
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
    text-decoration: none;

    &:hover {
      color: $primary;
    }
  }

  .calendar-slider-item__date {
    color: #2d3a4b;
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
  }
}