.slider {
  position: relative;

  margin-top: 40px;
  margin-bottom: 40px;

  &.slider_news {
    max-width: 760px;
    margin-left: auto;
    margin-right: auto;
  }

  .slider__init {}

  .slider__img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  .slider__item {
    display: block !important;
  }

  .slider__button_left {
    left: 0;

    background-color: white;
    color: rgba(#000, 0.15);

    @include media-xs {
      left: 5px;
    }
  }

  .slider__button_right {
    left: 95px;
    background-color: $primary;
    color: #000;

    @include media-xs {
      left: inherit;
      right: 5px;
    }
  }

  .slider__button {
    position: absolute;
    top: 0;
    z-index: 2;

    display: flex;
    align-items: center;
    flex-shrink: 0;
    justify-content: center;

    width: 95px;
    height: 131px;
    padding: 0;

    background: none;
    border: none;

    cursor: pointer;

    @include media-xs {
      top: 50%;
      bottom: inherit;

      width: 24px;
      height: 24px;
      margin-top: -12px;

      background-color: $primary;

      font-size: 12px;
      color: #000;
    }

    &:hover {

      opacity: .8;
    }

    font-size: 18px;
  }
}