.calendar-panel {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  width: 100%;

  .calendar-panel__nav {
    display: flex;
    flex-wrap: wrap;

    a {
      position: relative;
      display: block;
      margin-bottom: 51px;

      color: $dark_text;
      font-size: 18px;
      font-weight: 700;
      line-height: 20px;
      text-decoration: none;
      @include tracking(25);

      &.active,
      &:hover {
        color: $primary;
      }

      &.active:after {
        content: '';

        position: absolute;
        bottom: -19px;
        left: 0;

        width: 66px;
        height: 3px;
        background-color: $primary;
      }

      + a {
        margin-left: 58px;
      }
    }

    @include media(567px) {
      width: 100%;

      margin-top: 30px;
      margin-bottom: 0;

      a {
        margin-bottom: 30px;
        width: 100%;

        + a {
          margin-left: 0;
        }
      }
    }
  }

  .calendar-panel__view {
    @include media(567px) {
      margin-bottom: 30px;
    }

    a {
      color: $dark_text;
      font-size: 18px;
      text-decoration: none;

      &.active,
      &:hover {
        color: $primary;
      }

      + a {
        margin-left: 30px;
      }
    }
  }
}

.upload-more {
  margin-top: 27px;
  text-align: center;
}

.download-box {
  margin-top: 3px;

  display: flex;
  align-items: center;

  text-decoration: none;

  @include media-xs {
    width: 100%;
    max-width: 315px;
    margin-right: auto;
    margin-left: auto;
  }

  .download-box__icon {
    flex-shrink: 0;

    width: 36px;
    margin-right: 17px;
    padding: 4px 2px;

    background-color: $gold;

    color: #fff;
    font-size: 12px;
    line-height: 13px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;

    @include tracking(30);
  }

  .download-box__link {
    display: block;
    max-width: calc(100% - 53px);
    padding-bottom: 7px;

    border-bottom: 1px dashed $primary;

    color: #a6a6a6;
    font-size: 11px;
    text-transform: uppercase;
    @include tracking(25);
  }

  .download-box__arrow {
    display: block;
    width: 90px;
    height: 14px;
    margin-left: 24px;

    background: url('../images/icons/right-btn-arrow.png') center right no-repeat;
  }

  + .calendar-box {
    margin-top: 43px;
    margin-bottom: 30px;
  }
}

.calendar-box {
  padding-left: 13px;
  padding-right: 13px;
  max-width: 315px;
  margin-bottom: 40px;

  @include media-xs {
    margin-right: auto;
    margin-left: auto;
  }

  .calendar-box__title {
    margin-bottom: 32px;

    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    text-align: center;

    @include tracking(25);
  }

  .xdsoft_datetimepicker {
    width: 100%;
    padding: 0;

    border: none;
    box-shadow: none;

    .xdsoft_today_button {
      display: none;
    }

    .xdsoft_calendar {
      th {
        padding-top: 12px;
        padding-bottom: 6px;

        background: none;
        border-style: none;

        color: $primary;
        font-size: 12px;
        font-weight: 700;
        line-height: 28px;
        text-transform: lowercase;
        border-bottom: 2px solid #d1d5db;
      }

      td {
        background: none;
        border: none;
        padding-top: 10px;
        padding-bottom: 10px;

        font-size: 10px;
        font-weight: 700;
        line-height: 25px;
        text-align: center;

        div {
          width: 25px;
          height: 25px;
          margin-right: auto;
          margin-left: auto;

          border-radius: 50%;
          line-height: 25px;
          padding: 0;
          text-align: center;
        }

        &:hover {
          background: #fff !important;

          div {
            background: $primary;
          }
        }

        &.xdsoft_current {
          box-shadow: none;
          background: none;

          div {

            background: $primary;
          }
        }
      }
    }

    .xdsoft_prev,
    .xdsoft_next {
      width: 8px;
      height: 14px;

      background: none;
    }

    .xdsoft_prev {
      background: url('../images/icons/arrow-left.png') 0 0 no-repeat;
    }

    .xdsoft_next {
      background: url('../images/icons/arrow-right.png') 0 0 no-repeat;
    }

    .xdsoft_monthpicker {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 7px;

       * > {
         float: none;
       }
    }

    .xdsoft_month {
      width: auto;

      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase;
      text-align: center;

      @include tracking(200);

      i {
        display: none;
      }
    }

    .xdsoft_datepicker {
      width: 100%;
      margin: 0;
    }

    .xdsoft_year {
      display: none;
    }
  }
}

.slider-news-box {
  max-width: 315px;
  margin-top: 40px;

  @include media-md {
    margin-top: 0;
    margin-bottom: 40px;
  }

  .slider-news-box__title {
    margin-bottom: 10px;

    font-size: 18px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
  }

  .slider-news-box__next {
    width: 8px;
    height: 14px;
    margin: 10px auto 0;

    background: url('../images/icons/arrow-right.png') 0 0 no-repeat;
    cursor: pointer;

    transform: rotate(90deg);

    &:hover {
      opacity: 0.68;
    }
  }
}

.calendar-details-page {}

.banner {
  width: 100%;
  height: 385px;

  background-position: 0 0 !important;
  background-repeat: no-repeat !important;

  @include media(600px) {
    height: 200px;
  }
}

.content-section {
  max-width: 780px;
  margin-right: auto;
  margin-left: auto;



  p {
    color: #666;

    margin: 0 0 7px;

    font-size: 14px;
    line-height: 25px;
    text-align: justify;
  }

  .content-section__start {
    position: relative;
  }

  .content-section__date {
    position: absolute;
    top: -12px;
    left: -168px;

    font-size: 14px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;

    @include tracking(25);

    span {
      display: block;
      width: 150px;

      font-size: 79px;
    }

    @include media(1024px) {
      position: static;
      margin-bottom: 20px;
      text-align: center;

      span {
        width: auto;
      }
    }
  }

  .content-section__list {
    margin-top: 43px;
    margin-bottom: 36px;
    padding-top: 28px;

    border-top: 2px solid $border;
  }

  .button-white-arrow {
    margin-top: 27px;
  }
}

.content-section_news {
  .title {
    margin-bottom: 50px;
  }

  p {
    margin-bottom: 25px;
  }
}

.details-page {
  .content {
    padding-top: 45px;
  }

  .page-title {
    margin-bottom: 33px;


    text-transform: none;

    @include media(null, 768px) {
      font-size: 36px;
    }
  }
}

.sub-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  margin-bottom: 16px;

  @include media(1024px) {
    margin-bottom: 0;
  }
}

.place-marker {
  position: relative;

  padding-left: 21px;
  margin-bottom: 15px;

  font-size: 16px;
  font-weight: 700;
  line-height: 20px;

  span {
    display: block;
    text-transform: uppercase;
  }

  i {
    position: absolute;
    top: 2px;
    left: 0;

    font-size: 16px;
    color: $primary;
  }
}

.place-url {
  margin-bottom: 15px;

  font-size: 16px;
  line-height: 21px;

  @include tracking(50);
}

.price-line {
  margin: 34px 0 20px;
  text-align: center;

  &:before {
    content: '';

    display: block;
    width: 66px;
    height: 3px;
    margin-right: auto;
    margin-left: auto;

    background-color: $primary;
  }

  .price-line__count {
    margin-top: 28px;
    margin-bottom: 30px;

    font-size: 21px;
    font-weight: 700;
    text-transform: uppercase;

    @include tracking(25);
  }
}