* {
	box-sizing: border-box;

    font-family: $base;

	&,
	&::before,
	&::after {
		box-sizing: border-box;
	}

	&:focus,
	&:active {
		outline: none;
	}
}

body {
	color: $dark_text;
}


.title {
	margin: 0 0 26px;
	color: #2a2d2e;
	font-size: 48px;
	font-weight: 700;
	line-height: 54px;
	@include tracking(25);

	@include media-sm {
		font-size: 30px;
		line-height: 36px;
	}

	@include media(567px) {
		font-size: 24px;
		line-height: 30px;
	}
}

.text {
	color: $dark_text;
	font-size: 12px;
	line-height: 18px;
}


.flex {
	display: flex;
}

.flex-align-center {
	@extend .flex;
	align-items: center;
}

.flex-wrap {
	@extend .flex;
	flex-wrap: wrap;
}

.flex-space-between {
	@extend .flex;
	justify-content: space-between;
}

.mb20 {
	margin-bottom: 20px;
}

.mb30 {
	margin-bottom: 30px;
}

.mb40 {
	margin-bottom: 40px;
}

.mb40 {
	margin-bottom: 40px;
}

.mb60 {
	margin-bottom: 60px;
}

.img-responsive {
	max-width: 100%;
	height: auto;
}