.content {
  padding-top: 65px;
  padding-bottom: 50px;
}


.page-title {
  margin: 0 0 62px;

  color: $dark_text;
  font-size: 43px;
  font-weight: 700;
  line-height: 44px;
  text-transform: uppercase;

  @include tracking(25);

  @include media-xs {
    font-size: 30px;
    line-height: 32px;
  }

  @include media(567px) {
    font-size: 24px;
    line-height: 26px;
  }
}