.checkbox {
  user-select: none;

  .checkbox__input {
    display: none;
  }

  .checkbox__label {
    position: relative;

    display: block;
    min-height: 20px;
    padding-left: 31px;
  }

  .checkbox__icon {
    position: absolute;
    top: 1px;
    left: 0;
    z-index: 1;

    width: 21px;
    height: 21px;
    background-color: white;
    border: 2px solid #e1e1e1;

    transition: all 0.35s ease;

    &:before {
      display: block;
      content: '';
      position: absolute;
      top: 3px;
      left: 1px;

      width: 8px;
      height: 11px;

      border-width: 2px;
      border-style: solid;
      border-color: transparent #231F20 #231F20 transparent;
      transform: rotateZ(37deg);
      transform-origin: 100% 100%;
      opacity: 0;
      transition: all 0.35s ease;
    }
  }

  .checkbox__text {
    color: $dark_text;
    font-size: 12px;
    font-weight: 700;
    line-height: 21px;

    &:hover {
      color: $primary;
    }
  }

  .checkbox__input:checked + .checkbox__label {

    .checkbox__icon {
      &:before {
        opacity: 1;
      }
    }
  }

  &.checkbox_inline {
    display: inline-block;
  }

  &.checkbox_big {
    .checkbox__icon {
      top: 4px;
    }

    .checkbox__text {
      padding-top: 0;

      font-size: 24px;
      line-height: 26px;
    }
  }

  &.checkbox_right {

    .checkbox__label {
      padding-right: 26px;
      padding-left: 0;
    }

    .checkbox__icon {
      left: inherit;
      right: 0;
    }
  }

  &.checkbox_disabled {
    .checkbox__text {
      color: $border;
    }

    .checkbox__icon {
      &:before {
        border-color: transparent $border $border transparent;
      }
    }
  }
}