.container {
  @include media(null, 1440px) {
    width: 1270px;
  }
}

.main-bg {
  background: #000 url('../images/bg/main.jpg') center center no-repeat;
  background-size: cover;

  color: #fff;
}

.main-page {
  .menu-toggle {
    position: fixed;
  }

  .header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;

    width: 100%;

    background-color: transparent;

    @include media(568px) {
      background-color: #111214;
    }
  }

  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 5;

    width: 100%;
    height: 99px;

    border-top: none;

    @include media-xs {
      display: none;
    }
  }
}

.section {
  overflow: hidden;

  .section__container {
    position: relative;
    z-index: 2;
  }

  .section__info {
    width: 820px;

    @include media(1440px) {
      width: 540px;
    }

    @include media-xs {
      width: 100%;
    }

    .button {
      margin: 60px 0 0 192px;
      min-width: 187px;

      background: none;

      color: #fff;

      @include media(1440px) {
        margin-left: 100px;
        margin-top: 30px;
      }

      @include media(567px) {
        margin-left: 0;
      }
    }
  }

  .section__title {
    padding: 13px 0 20px 49px;
    margin-bottom: 29px;

    border-left: 14px solid #a17948;

    color: #fff;
    font-size: 77px;
    font-weight: 700;
    line-height: 78px;
    text-transform: uppercase;
    @include tracking(25);

    @include media(1440px) {
      border-left-width: 7px;

      font-size: 47px;
      line-height: 48px;
    }

    @include media-xs {
      font-size: 30px;
      line-height: 32px;

      padding-left: 30px;
    }

    @include media(567px) {
      font-size: 24px;
      line-height: 26px;
    }
  }

  .section__text {
    max-width: 480px;
    margin-left: 193px;

    color: #fff;
    font-size: 18px;
    line-height: 30px;

    @include media(1440px) {
      margin-left: 100px;
    }

    @include media(567px) {
      margin-left: 0;

      font-size: 16px;
      line-height: 20px;
    }
  }

  &:before {
    content: '';
    position: absolute;
    z-index: 1;

    @include media(640px) {
      display: none;
    }
  }

  &.section_1 {
    &:before {
      top: 50%;
      left: 1151px;

      width: 561px;
      height: 543px;
      margin-top: -40px;

      background: url('../images/main/1.png') 0 0 no-repeat;

      transform: translateY(-50%);

      @include media(1440px) {
        margin-top: 0;
        left: 820px;
        transform: translateY(-50%) scale(0.7);
      }

      @include media(1280px) {
        left: 600px;
      }
    }
  }

  &.section_2 {
    &:before {
      top: 50%;
      left: 1150px;

      width: 543px;
      height: 591px;
      margin-top: -48px;

      background: url('../images/main/2.png') 0 0 no-repeat;

      transform: translateY(-50%);

      @include media(1440px) {
        margin-top: 0;
        left: 820px;
        transform: translateY(-50%) scale(0.7);
      }

      @include media(1280px) {
        left: 600px;;
      }
    }
  }

  &.section_3 {
    &:before {
      top: 50%;;
      left: 1129px;

      width: 800px;
      height: 773px;
      margin-top: 0;

      background: url('../images/main/3.png') 0 0 no-repeat;

      transform: translateY(-50%);

      @include media(1440px) {
        left: 600px;
        margin-top: -130px;

        transform: translateY(-50%) scale(0.7);
      }

      @include media(1280px) {
        left: 430px;
        margin-top: -50px;
      }

      @include media(null, 1441px) {
        top: 0;
        transform: none;
      }
    }
  }

  &.section_4 {
    &:before {
      top: 50%;
      left: 1035px;

      width: 739px;
      height: 962px;
      margin-top: -61px;

      background: url('../images/main/4.png') 0 0 no-repeat;

      transform: translateY(-50%);

      @include media(1400px) {
        transform: translateY(-50%) scale(0.7);
      }

      @include media(1440px) {
        left: 705px;
        margin-top: -6px;
        transform: translateY(-50%) scale(0.75);
      }

      @include media(1280px) {
        top: 0;
        left: 500px;

        margin-top: 0;
        width: 500px;
        height: 527px;

        background: url('../images/main/4_small.png') 0 0 no-repeat;

        transform: scale(1);
      }
    }
  }

  &.section_5 {
    &:before {
      top: 50%;
      left: 1145px;

      width: 544px;
      height: 827px;

      background: url('../images/main/5.png') 0 0 no-repeat;

      transform: translateY(-50%);

      @include media(1440px) {
        left: 870px;

        transform: translateY(-50%) scale(0.7);
      }

      @include media(1280px) {
        left: 681px;
      }

      @include media(1024px) {
        left: 530px;
        top: inherit;
        bottom: 0;

        transform: scale(0.7);
      }
    }
  }
}

.navigation {
  position: fixed;
  bottom: 100px;
  left: 0;
  z-index: 3;

  width: 100%;
  padding-bottom: 40px;

  @include media-xs {
    display: none;
  }

  @include media(1440px) {
    padding-bottom: 20px;
  }

  .navigation__counter {
    display: flex;
    justify-content: space-between;

    float: left;
    width: 260px;

    @include media(1440px) {
      width: 195px;
    }

    @include media-sm {
      display: none;
    }
  }

  .navigation__counter-value {
    color: #bd955d;
    font-size: 70px;
    font-weight: 700;
    line-height: 73px;

    @include tracking(-100);

    @include media(1440px) {
      font-size: 47px;
      line-height: 48px;
    }
  }

  .navigation__counter-step {
    display: flex;

    margin-top: 36px;

    @include media(1440px) {
      margin-top: 23px;
    }

    span {
      display: block;
      width: 31px;
      height: 3px;

      @include media(1440px) {
        width: 24px;
      }

      &:nth-child(1) {
        background-color: #fff;
      }

      &:nth-child(2) {
        background-color: rgba(#fff, 0.8);
      }

      &:nth-child(3) {
        background-color: rgba(#fff, 0.6);
      }

      &:nth-child(4) {
        background-color: rgba(#fff, 0.4);
      }

      &:nth-child(5) {
        background-color: rgba(#fff, 0.2);
      }

      &.active {
        background-color: $gold;
      }
    }
  }

  .navigation__line {
    display: flex;
    justify-content: space-between;

    float: left;
    width: 663px;
    padding-top: 5px;
    margin-left: 87px;

    @include media(1440px) {
      margin-left: 40px;
    }

    @include media-sm {
      margin-left: 0;
    }
  }

  .navigation__item {
    flex-shrink: 0;

    width: 100%;
    max-width: 110px;

    &:hover {
      opacity: .8;
      cursor: pointer;
    }
  }

  .navigation__number {
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;

    @include tracking(50);

    @include media(1440px) {
      font-size: 18px;
      line-height: 22px;
    }

    &:after {
      content: '';

      display: block;
      width: 66px;
      height: 3px;
      margin-top: 7px;
      margin-bottom: 10px;
      background-color: #fff;

      @include media(1440px) {
        margin-top: 2px;
        margin-bottom: 5px;
      }
    }
  }

  .navigation__text {
    color: #fff;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;

    @include tracking(50);

    @include media(1440px) {
      font-size: 12px;
      line-height: 14px;
    }
  }

  .active {
    .navigation__number {
      color: $gold;

      &:after {
        background-color: $gold;
      }
    }

    .navigation__text {
      color: $gold;
    }
  }
}