.register {
  overflow: hidden;

  .register__title {
    position: relative;
    z-index: 2;

    width: 100%;
    margin-bottom: 75px;

    color: $dark_text;
    font-size: 36px;
    font-weight: 700;
    text-align: center;
    @include tracking(25);

    &.register__title_first {
      margin-bottom: 35px;
    }

    @include media-xs {
      font-size: 30px;
      line-height: 32px;

      margin-bottom: 40px;
    }
  }

  .register__line {
    @extend .container;
    display: flex;
    flex-wrap: wrap;

    @include media-xs {
      padding-right: 0;
      padding-left: 0;
    }
  }

  .register__left {
    position: relative;
    display: flex;
    flex-direction: column;

    width: calc(100% - 430px);
    padding-top: 167px;

    @include media-md {
      padding-top: 80px;
    }

    &:before {
      content: '';

      position: absolute;
      top: 0;
      right: 0;
      z-index: 0;

      width: 10000px;
      height: 100%;

      background-color: #fff;

      @include media-sm {
        display: none;
      }
    }

    @include media-sm {
      width: 100%;
      padding-top: 0;
      padding-bottom: 40px;
    }
  }

  .register__right {
    display: flex;
    width: 430px;
    padding-top: 167px;
    padding-bottom: 96px;

    @include media-md {
      padding-top: 80px;
    }

    @include media-sm {
      display: none;
    }
  }

  .register__step {
    position: relative;
    z-index: 2;

    padding-left: 73px;

    + .register__step {
      margin-top: 49px;
    }
  }

  .active {
    .register__number {
      background-color: $primary;
      border-color: $primary;

      color: #fff;
    }
  }

  .register__number {
    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    top: 0;
    left: -28px;

    width: 52px;
    height: 52px;

    background-color: #fff;
    border: 2px solid #e1e1e1;
    border-radius: 50%;

    color: #e1e1e1;
    font-size: 20px;
    font-weight: 700;
  }

  .register__name {
    color: #fff;
    font-size: 20px;
    font-weight: 700;

    @include tracking(25);
  }

  .register__text {
    margin-top: 18px;

    color: #fff;
    font-size: 14px;
    line-height: 22px;
  }

  .form {
    height: 100%;

    @include media-sm {
      padding-top: 35px;
      padding-bottom: 95px;
    }
  }

  .form-navigation {
    position: absolute;
    left: 0;
    bottom: 43px;
    z-index: 2;

    width: 100%;
    padding-right: 35px;
    padding-left: 35px;

    @include media(567px) {
      position: static;
      text-align: center;

      button {
        float: none !important;

        + button {
          margin-top: 25px;
        }
      }
    }
  }

  .form-previous,
  .form-next {
    @include media-md {
      padding-right: 0;
      padding-left: 0;
      &:before {
        display: none;
      }
    }
  }
}

.thanks {
  padding-top: 159px;
  padding-bottom: 159px;

  text-align: center;

  @include media(1400px) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  @include media-xs {
    padding-top: 40px;
  }

  .thanks__title {
    margin-top: 22px;

    color: #fff;
    font-size: 36px;
    font-weight: 700;
    @include tracking(25);

    @include media-xs {
      font-size: 30px;
      line-height: 32px;
    }
  }

  .thanks__text {
    max-width: 343px;
    margin: 41px auto;

    color: #fff;
    font-size: 14px;
    line-height: 22px;
  }

  .next {
    color: #fff;

    &:hover {
      color: $primary;
    }
  }
}