.upload-field {
  .upload-field__input {
    display: none;
  }

  .upload-field__img-box {
    width: 105px;
    height: 105px;
    margin: 0 auto 19px;

    border-radius: 50%;
    overflow: hidden;
  }

  .upload-field__img {
    display: block;
    width: 100%;
    height: 100%;

    object-fit: cover;
  }

  .upload-field__btn {
    margin-right: auto;
    margin-left: auto;
    display: block;
    width: 174px;
    padding-right: 10px;
    padding-left: 10px;
  }

  .upload-field__label {}

  .upload-field__submit {
    display: none;
  }

  &.uploaded {
    .upload-field__label {
      display: none;
    }

    .upload-field__submit {
      display: block;
      margin-right: auto;
      margin-left: auto;
    }
  }
}