.footer  {
  padding-top: 28px;
  padding-bottom: 29px;

  border-top: 1px solid $light-gray;
  background-color: #fff;

  .footer__lang {
    float: left;
    margin-top: 4px;
    margin-right: 35px;
  }

  .footer__copy {
    float: left;

    font-size: 16px;
    line-height: 21px;
    @include tracking(50);

    @include media-xs {
      width: 100%;
      margin-top: 10px;
      text-align: center;
    }
  }

  .footer__links {
    float: right;
    text-align: right;

    a {
      color: $dark_text;
      font-size: 16px;
      line-height: 21px;
      text-decoration: none;
      @include tracking(50);
    }
  }

  .footer__address {
    float: right;
    margin-right: 113px;

    a {
      display: inline-block;
      padding: 5px 0 8px;

      color: $dark_text;
      font-weight: 700;
      text-decoration: none;
      @include tracking(25);

      border-bottom: 1px dashed $dark_text;

      &:hover {
        color: $primary;
      }
    }

    @include media(1024px) {
      display: none;
    }

    @include media-xs {
      display: block;
      width: 100%;
      margin-right: 0;

      text-align: center;
    }
  }
}