.radio {
  user-select: none;

  .radio__input {
    display: none;
  }

  .radio__label {
    position: relative;

    display: block;
    min-height: 16px;
    padding-left: 20px;
  }

  .radio__icon {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    width: 16px;
    height: 16px;

    background-color: #fff;
    border: 2px solid $primary;
    border-radius: 50%;

    transition: all 0.35s ease;

    &:after {
      content: '';

      position: absolute;
      top: 50%;
      left: 50%;

      width: 7px;
      height: 7px;

      border-radius: 50%;

      transform: translate(-50%, -50%);
    }
  }

  .radio__text {
    color: $dark_text;
    font-size: 16px;
    line-height: 16px;

    &:hover {
      color: $primary;
    }
  }

  .radio__input:checked + .radio__label {

    .radio__icon:after {
      background-color: $primary;
    }
  }

  &.radio_inline {
    display: inline-block;
  }
}
