.card-wrapper {
  float: left;
  width: 50%;
  padding-right: 15px;
  padding-left: 15px;

  @include media(567px) {
    width: 100%;
  }
}

.calendar-item {
  &.calendar-item_grid {
    margin-bottom: 32px;
  }

  .calendar-item__img-box {
    display: block;
  }
  .calendar-item__img {
    max-width: 100%;
    height: auto;
  }

  .calendar-item__title {
    height: 96px;
    margin-top: 20px;
    margin-bottom: 20px;

    overflow: hidden;

    color: $dark_text;
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    @include tracking(25);

    @include media(640px) {
      font-size: 16px;
      line-height: 18px;
    }

    @include media(567px) {
      height: auto;
    }
  }

  .calendar-item__other {
    position: relative;

    display: flex;
    align-items: center;
    flex-wrap: wrap;

    padding-top: 22px;

    &:before {
      content: '';

      position: absolute;
      top: 0;
      left: 0;

      height: 1px;
      width: 202px;

      background-color: #d1d5db;
    }
  }

  .calendar-item__date,
  .calendar-item__place {
    display: flex;
    align-items: center;
    padding-left: 3px;

    font-size: 12px;

    i {
      margin-right: 8px;

      color: $primary;
      font-size: 18px;
    }

    span {
      font-weight: 700;
    }
  }

  .calendar-item__date {
    margin-right: 22px;
  }

  .calendar-item__place {}
}
