.collapse {
  &.active {
    .collapse__btn {
      display: inline-block;

      cursor: pointer;

      &:before {
        transform: rotate(-90deg);
      }

      &:hover {
        .collapse__btn {
          color: $primary;
        }
      }
    }

    .collapse__box {
      display: block;
    }
  }

  .collapse__btn {
    position: relative;

    padding: 14px 20px 14px 48px;

    color: $dark_text;
    font-size: 21px;
    font-weight: 700;
    text-transform: uppercase;

    @include tracking(25);

    transition: color 0.35s ease;

    @include media-xs {
      font-size: 18px;
    }

    @include media(567px) {
      font-size: 16px;
      padding-left: 32px;
    }

    &:before {
      content: '';

      position: absolute;
      top: 17px;
      left: 9px;

      width: 8px;
      height: 14px;
      background: url('../images/icons/arrow-right.png') 0 0 no-repeat;

      transform: rotate(90deg);
    }
  }

  .collapse__box {
    display: none;
    padding: 19px 0;
  }
}