.calendar-list-item {
  padding-bottom: 19px;

  + .calendar-list-item {
    border-top: 1px solid #d1d5db;
  }

  .calendar-list-item__title {
    margin-top: 20px;
    margin-bottom: 20px;
    display: inline-block;

    overflow: hidden;

    color: $dark_text;
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    text-decoration: none;

    @include tracking(25);

    &:hover {
      color: $primary;
    }

    @include media-xs {
      font-size: 18px;
      line-height: 20px;
    }
  }

  .calendar-list-item__other {
    position: relative;

    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .calendar-list-item__date,
  .calendar-list-item__place {
    position: relative;

    display: flex;
    align-items: center;
    padding-left: 27px;
    margin-bottom: 5px;

    font-size: 12px;
    line-height: 16px;

    i {
      position: absolute;
      top: 0;
      left: 3px;

      margin-right: 8px;

      color: $primary;
      font-size: 18px;
    }

    span {
      font-weight: 700;
    }
  }

  .calendar-list-item__date {
    margin-right: 22px;
  }

  .calendar-list-item__place {
    i {
      left: 5px;
    }
  }


}
