.popup {
  position: relative;

  max-width: 434px;
  padding: 30px 30px 36px;
  margin-right: auto;
  margin-left: auto;

  background-color: white;
  box-shadow: 0 17px 49px rgba(2, 2, 2, 0.51);

  .popup__close {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 2;

    width: 15px;
    height: 15px;
    padding: 0;

    background: url('../images/icons/close.png') 0 0 no-repeat;
    border: none;
    cursor: pointer;

    &:hover {
      opacity: .8;
    }
  }

  .popup__title {
    margin: 7px 0 24px;

    font-size: 18px;
    font-weight: 700;
    line-height: 20px;
    text-align: center;
  }

  .popup__text {
    font-size: 12px;
    font-weight: 700;
    line-height: 18px;
    text-align: center;
  }

  .popup__other-text {
    color: #c6c5c5;
    font-size: 10px;
    font-weight: 700;
    line-height: 14px;
    text-align: center;
  }

  .upload-field {
    margin-top: 18px;
    margin-bottom: 19px;
  }
}

.popup.popup_form {
  padding: 45px 50px 50px;

  @include media-xs {
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 40px;
  }
}

.popup.popup_success {
  display: flex;
  align-items: center;
  justify-content: center;

  min-height: 277px;

  .popup__text {
    margin-bottom: 25px;

    font-size: 18px;
    line-height: 24px;
  }
}