.form.form_login {
  padding-top: 10px;

  .form__checkbox {
    margin-top: 17px;
    margin-bottom: 22px;
  }

  .form__field {
    margin-bottom: 9px;
  }

  .form__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-bottom: 10px;

    @include media(567px) {
      display: block;
      text-align: center;
    }

    a, button {
      display: inline-block;
      margin-bottom: 10px;
    }
  }
}

.forgot-link {
  display: inline-block;

  border-bottom: 1px dashed $dark_text;

  color: $dark_text;
  font-size: 14px;
  font-weight: 700;
  line-height: 32px;
  text-decoration: none;

  @include tracking(50);

  &:hover {
    color: $primary;
  }
}