.form.form_personal {
  position: relative;

  max-width: 435px;
  margin-right: auto;
  margin-left: auto;

  .form__avatar {
    position: absolute;
    left: -169px;
    top: 0;

    display: flex;
    flex-direction: column;
    align-items: center;

    width: 150px;
    height: 150px;

    .avatar {
      margin-bottom: 15px;
    }

    @include media-xs {
      position: static;
      height: auto;
      margin: 0 auto 25px;
    }
  }

  .form__changes-btn {
    padding: 0;
    width: 90px;
    display: block;
    margin-left: auto;
    margin-right: auto;

    background: none;
    border: none;
    cursor: pointer;

    color: $dark_text;

    text-align: left;

    &:hover {
      color: $primary;
    }

    > * {
      display: inline-block;
      vertical-align: middle;
    }

    i {
      width: 18px;
      text-align: center;
      font-size: 17px;
      line-height: 18px;
      margin-right: 7px;
    }

    span {
      font-size: 12px;
      font-weight: 700;
      line-height: 14px;
    }

    + .form__changes-btn {
      margin-top: 2px;
    }
  }

  .form__place-line {
    @include clearfix;

    margin-right: -5px;
    margin-left: -5px;

    .form__field {
      float: left;
      width: 33.33%;
      padding-right: 5px;
      padding-left: 5px;

      @include media(567px) {
        width: 50%;

        &.form__field_street {
          width: 100%;
        }
      }
    }

    .form__field_other {
      float: left;
      width: 16.66%;
      padding-right: 5px;
      padding-left: 5px;

      @include media(567px) {
        width: 50%;
      }
    }
  }

  .form__btn {
    display: block;
    margin: 30px auto 0;
    width: 245px;
  }

  .form__with-icon {
    position: relative;

    + .form__with-icon {
      margin-top: 9px;
    }
  }

  .form__with-right-icon {
    .form__input {
      padding-right: 48px;
    }

    .form__icon {
      right: 0;
    }
  }

  .form__with-left-icon {
    .form__input {
      padding-left: 48px;
    }

    .form__icon {
      left: 0;
    }
  }

  .form__icon {
    position: absolute;
    top: 0;

    display: flex;

    width: 48px;
    height: 48px;

    span {
      display: block;
      margin: auto;

      font-size: 18px;
    }
  }
}