.personal-menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  width: 100%;
  margin-top: -17px;
  margin-bottom: 42px;
  padding-bottom: 28px;

  border-bottom: 2px solid $border;

  @include media-sm {
    padding-bottom: 0;
  }

  .personal-menu__col {
    margin-right: 36px;
    margin-left: 36px;

    @include media-md {
      margin-right: 15px;
      margin-left: 15px;
    }

    @include media-sm {
      margin-bottom: 30px;
    }

    @include media(375px) {
      width: 100%;

      text-align: center;
    }
  }

  .personal-menu__link {
    position: relative;

    color: $dark_text;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    text-decoration: none;
    text-transform: uppercase;

    @include tracking(25);

    &:hover {
      color: $primary;
    }

    &.active:after {
      content: '';

      position: absolute;
      top: 43px;
      left: 50%;

      width: 66px;
      height: 3px;
      background-color: $primary;
      margin-left: -33px;

      @include media-sm {
        top: 23px;
      }
    }
  }
}